.page-footer__map {
  padding: 0;
  margin: 0;
  background-color: $lightgrey;

  &--index {
    background-color: $white;
  }
}

.page-footer__color-wrap {
  width: 100%;
  background-color: $lightgrey;
}

.page-footer__wrap {
  @include container;

  padding-top: 39px;
  padding-bottom: 20px;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 55px;
    padding-bottom: 61px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 59px;
  }
}

.page-footer__logo {
  display: block;
  padding: 0 0 30px 0;
  text-align: center;

  @media (min-width: $tablet-width) {
    padding: 0;
  }

  &:active {
    opacity: 30%;
  }
}
