.product {
  position: relative;
  padding-top: 18px;
  padding-bottom: 24px;

  @media (min-width: $tablet-width) {
    padding: 0 37px 40px 37px;
    margin: 70px 0;

    background-color: $lightgrey;
  }

  @media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
    &:nth-last-child(-n+2) {
      margin-bottom: 0;
    }
  }

  &::after {
    content: "";
    position: absolute;
    height: 1px;
    width: 100vw;
    bottom: 0;
    left: -20px;
    background-color: $lightgrey3;

    @media (min-width: $tablet-width) {
      content: none;
    }
  }

  &--more {
    text-align: center;

    &::before {
      content: "";
      position: static;
      display: block;
      margin: 0 auto;
      margin-bottom: 23px;
      margin-top: 5px;
      width: 30px;
      height: 30px;
      background-image: url("../../img/icon__plus.svg");
      background-size: 30px 30px;
    }

    @media (min-width: $tablet-width) {
      background-color: $white;
      border: 1px solid #e5e5e5;

      &::before {
        width: 60px;
        height: 60px;
        background-size: 60px 60px;
        margin: 50px auto;
      }
    }
  }
}

.product__wrapper {
  display: flex;
  align-items: center;

  @media (min-width: $tablet-width) {
    flex-direction: column;
    align-items: stretch;
  }
}

.product__wrap-img {
  width: 50%;
  text-align: center;

  @media (min-width: $tablet-width) {
    position: absolute;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
  }

  &--small {
    @media (min-width: $tablet-width) {
      margin-top: -42px;
    }

    @media (min-width: $desktop-width) {
      margin-top: -50px;
    }
  }

  &--big {
    @media (min-width: $tablet-width) {
      margin-top: -72px;
    }

    @media (min-width: $desktop-width) {
      margin-top: -80px;
    }
  }
}

.product__wrap {
  @media (max-width: $mobile-width-only) {
    width: 50%;
    padding-bottom: 9px;
  }
}

.product__title {
  @include a-reset;
  display: block;
  padding-right: 64px;
  padding-bottom: 18px;

  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: $darkgrey3;
  text-transform: uppercase;

  &--more {
    padding-right: 0;
    padding-bottom: 8px;
    color: $dark;
    margin: 0;
  }

  @media (min-width: $tablet-width) {
    padding-top: 162px;
    padding-right: 0;
    padding-bottom: 16px;


    font-size: 20px;
    line-height: 24px;
    text-align: center;

    &--more {
      display: block;
      width: 134px;
      padding: 0 0 23px 0;
      margin: 0 auto;
    }
  }

  @media (min-width: $desktop-width) {
    line-height: 22px;
    padding-bottom: 20px;

    &--more {
      padding: 0 0 29px 0;
    }
  }
}

.product__weight {
  @media (min-width: $tablet-width) {
    display: block;
  }
}

.product__description {
  margin: 0;

  @media (min-width: $tablet-width) {
    margin-bottom: 20px;
  }
}

.product__values {
  display: flex;
  justify-content: space-between;

  @media (min-width: $tablet-width) {
    padding-top: 5px;
    padding-bottom: 7px;
    border-bottom: 1px solid #cdcdcd;
  }
}

.product__text {
  max-width: 75%;
  margin: 0;
  font-family: $text-font;
  font-size: 12px;
  line-height: 16px;
  color: $darkgrey2;

  &--more {
    padding-bottom: 21px;
    max-width: 100%;
  }

  &--value {
    flex-shrink: 0;
  }

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 18px;

    &--more {
      display: block;
      width: 167px;
      padding: 0 0 20px 0;
      margin-bottom: 62px;
      margin-left: auto;
      margin-right: auto;
    }
  }
}
