.main-catalog {
  @include container;
  padding-top: 27px;
  padding-bottom: 8px;

  @media (min-width: $tablet-width) {
    padding-top: 36px;
    padding-bottom: 62px;
  }

  @media (min-width: $desktop-small) {
    padding-top: 187px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 0;
  }
}

.main-catalog__title {
  @include text-reset;
  position: relative;
  padding-bottom: 41px;

  font-size: 36px;
  font-weight: 400;
  line-height: 1;

  @media (min-width: $tablet-width) {
    padding-bottom: 92px;
    font-size: 60px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 95px;
  }

  &::after {
    content: "";
    position: absolute;
    height: 1px;
    width: 100vw;
    bottom: 0;
    left: -20px;
    background-color: $lightgrey3;

    @media (min-width: $tablet-width) {
      content: none;
    }
  }
}

.main-catalog__wrap {
  @include ul-reset;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: start;
    column-gap: 60px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 80px;
    align-items: flex-start;
  }
}
