.map {
  @media (min-width: $desktop-width) {
    position: relative;
  }
}

.map__box {
  @media (min-width: $desktop-width) {
    @include container;
    position: relative;
  }
}

.map__box-adress {
  @include container;
  display: grid;
  grid-template-columns: 1fr 1fr;

  padding-top: 29px;
  padding-bottom: 26px;

  @media (min-width: $tablet-width) {
    padding: 67px 72px 58px 72px;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 93px;
    left: 29px;
    column-gap: 87px;
    width: 570px;
    padding: 66px 76px 56px 76px;

    z-index: 1;
    background: $white;
  }
}

.map__text {
  @include text-reset;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: $dark;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 25px;
  }
}

.map__adress {
  @include text-reset;
  font-family: $text-font;
  font-size: $small;
  line-height: 20px;
  color: $darkgrey2;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    text-align: right;
  }

  @media (min-width: $desktop-width) {
    text-align: left;
  }
}

.map__adress-city {
  @media (min-width: $tablet-width) {
    display: block;
    margin-top: 20px;
  }
}

.map__image {
  display: block;
  width: 100%;
  height: 362px;

  background-image: url("../img/map_mobile.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: $darkgrey;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../img/map_mobile@2x.png");
  }

  @media (min-width: $tablet-width) {
    height: 400px;
    background-image: url("../img/map_tablet.png");

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url("../img/map_tablet@2x.png");
    }
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/map_desktop.png");

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url("../img/map_desktop@2x.png");
    }
  }
}
